import props from './props'

const component = {
  name: 'SlideOut',
  props,
  data() {
    return {
      isVisible: false,
      // æ è¯é¼ æ æ¯å¦æä¸ï¼ä»æä¸æ¶è½æå¨å¤§å°
      mousedown: false,
      // é¼ æ æä¸æ¶çä½ç½®
      mouseDownPosition: {x: 0, y: 0},
      // å¼¹åºçåå§å¤§å°
      originSize: {width: 0, height: 0},
      // è°æ´å¤§å°æ¶çå¼ï¼åä½ä¸ºpx
      resizeValue: 0,
      // ç¶çº§åç´ 
      parentElement: null,
      // æ©å±æé®å®¹å¨
      extensionButtons: null,
      // body ç overflow å¼
      bodyOverflowValue: null
    }
  },
  watch: {
    visible(visible) {
      this.toggle(visible)
    },
    isVisible(v) {
      if (!this.isFixed) {
        // éåºå®æ¶ï¼ä¸éè¦éå®æ»å¨
        return
      }
      if (!this.bodyOverflowValue) {
        // åªéè¦è·åä¸æ¬¡
        this.bodyOverflowValue = window.getComputedStyle(document.body).overflow
      }
      if (v) {
        document.body.style.overflow = 'hidden'
      } else {
        document.body.style.overflow = this.bodyOverflowValue
      }
    }
  },
  computed: {
    dockOn() {
      return this.dock || 'right'
    },
    containerStyle() {
      let style = {
        // 2147483647 æ¯åè®¸çæå¤§å¼
        'z-index': this.fullscreen ? 2147483647 : this.zIndex
      }
      if (this.mousedown) {
        style.userSelect = 'none'
      }
      let distance = this.isVisible ? '0' : '100%'
      switch (this.dockOn) {
        case 'left':
          style.right = distance
          break
        case 'right':
          style.left = distance
          break
        case 'top':
          style.bottom = distance
          break
        case 'bottom':
          style.top = distance
          break
      }
      return style
    },
    maskStyle() {
      return this.maskColor ? {
        'background-color': this.maskColor
      } : {}
    },
    layoutStyle() {
      let style = {}
      let size = this.fullscreen ? '100%' : this.resizeValue > 0 ? `${this.resizeValue}px` : typeof this.size === 'number' ? `${this.size}px` : this.size
      let distance = this.isVisible || this.disableAnimation
        ? 0
        : (typeof this.size === 'number' || !(/%$/.test(this.size)) ? `${-parseInt(this.size)}px` : `${-parseInt(this.size)}%`)
      switch (this.dockOn) {
        case 'right':
          style.width = size
          style.right = distance
          break
        case 'left':
          style.width = size
          style.left = distance
          break
        case 'bottom':
          style.height = size
          style.bottom = distance
          break
        case 'top':
          style.height = size
          style.top = distance
          break
      }
      return style
    },
    containerClasses() {
      return {
        [this.customClass || '']: true,
        [`vue-slideout-dock-${this.dockOn}`]: true,
        'vue-slideout-visible': this.isVisible,
        'vue-slideout-enable-animation': !this.disableAnimation,
        'vue-slideout-show-header': this.title || this.$slots.header,
        'vue-slideout-show-footer': this.$slots.footer,
        'vue-slideout-fixed': this.isFixed,
        'vue-slideout-fullscreen': this.fullscreen
      }
    },
    headerStyle() {
      let style = {
        paddingRight: '0'
      }
      if (!this.extensionButtons) {
        return style
      }
      let btnStyle = window.getComputedStyle(this.extensionButtons)
      style.paddingRight = `${parseInt(btnStyle.width) + 5}px`
      return style
    },
    isFixed() {
      // æ¯å¦ä½¿ç¨åºå®å®ä½
      // è®¾ç½®äº appendTo çæ¶åï¼å°±ä¸åºå®æ¾ç¤º
      return this.fixed || !this.appendTo
    }
  },
  methods: {
    /**
     * è·åå³é­äºä»¶åæ°å¯¹è±¡
     */
    getArgs() {
      let me = this
      // éè¿ä½¿ç¨ setter ä»¥å®ç°å»¶è¿æä½
      return {
        // æ¯å¦ç­å¾æä½
        wait: false,
        set close(close) {
          // åæ¶å³é­
          if (!close) {
            return
          }
          // å³é­
          me.setVisibleValue(false)
        },
        get close() {
          return undefined
        }
      }
    },
    toggle(visible) {
      if (visible === this.isVisible) {
        return
      }
      // æ¾ç¤ºåè§¦åäºä»¶
      if (visible) {
        this.setVisibleValue(true)
        this.$nextTick(this.emitOpenEvent)
        return
      }
      // éèåè§¦åäºä»¶
      let args = this.getArgs()
      this.$emit('close', args)
      if (args.wait) {
        // éè¦ç­å¾ï¼æç»æ¯å¦å³é­è¦ç args.close æ¯å¦ä¸º true
        return
      }
      this.setVisibleValue(false)
    },
    setVisibleValue(visible) {
      // å¦ææ¾ç¤ºç¶æç¸åï¼åå¥ä¹ä¸å
      if (this.isVisible === visible) {
        return
      }
      // æ¾ç¤ºæ¶éç½®å¤§å°
      if (visible) {
        this.resizeValue = 0
      }
      this.isVisible = visible
      this.$el.focus()
      this.$emit('update:visible', visible)
      // è¥æå®äº .sync ä¿®é¥°ï¼åå³é­åéåºå¨å±
      this.$emit('update:fullscreen', false)
      // è§¦åå³é­åçäºä»¶
      if (!visible) {
        this.$nextTick(this.emitCloseEvent)
      }
    },
    appendComponentTo() {
      if (!this.appendTo) {
        this.parentElement = this.$el.parentElement
        return
      }
      let target = this.appendTo
      // handle selector
      if (typeof target === 'string') {
        target = document.querySelector(target)
        if (!target) {
          throw new Error(`SlideOut cannot found the node to append to: ${this.appendTo}`)
        }
      }
      target.appendChild(this.$el)
      this.parentElement = target
    },
    onMaskClick() {
      if (this.closeOnMaskClick) {
        this.toggle(false)
      }
    },
    getParentSize() {
      let rect = this.parentElement.getClientRects()[0]
      return {
        width: rect.width,
        height: rect.height
      }
    },
    /**
     * è·åå°æ­¤ç»ä»¶çå¤§å°ï¼åºäºpxï¼
     */
    getMyOwnSize() {
      let rect = this.$refs.layout.getClientRects()[0]
      return {
        width: rect.width,
        height: rect.height
      }
    },
    mouseDownHandler(e) {
      if (this.fullscreen) {
        // å¨å±æ¶ä¸åè®¸æ¹åå¤§å°
        return
      }
      this.mousedown = true
      this.mouseDownPosition = {
        x: e.pageX,
        y: e.pageY
      }
      this.originSize = this.getMyOwnSize()
    },
    mouseMoveHandler(e) {
      if (this.fullscreen) {
        // å¨å±æ¶ä¸åè®¸æ¹åå¤§å°
        return
      }
      // é¼ æ æªæä¸æ¶ï¼ä¸è½æå¨
      if (!this.mousedown) {
        return
      }
      e.preventDefault()
      // è·åé¼ æ çåç§»é
      let x = e.pageX - this.mouseDownPosition.x
      let y = e.pageY - this.mouseDownPosition.y

      let parentSize = this.getParentSize()
      let winSize = {
        width: window.innerWidth,
        height: window.innerHeight
      }
      let size = this.originSize
      let newSize = 0
      switch (this.dock) {
        case 'top':
          newSize = size.height + y
          if (newSize > parentSize.height) {
            newSize = parentSize.height
          }
          if (newSize > winSize.height) {
            newSize = winSize.height
          }
          break
        case 'right':
          newSize = size.width - x
          if (newSize > parentSize.width) {
            newSize = parentSize.width
          }
          if (newSize > winSize.width) {
            newSize = winSize.width
          }
          break
        case 'bottom':
          newSize = size.height - y
          if (newSize > parentSize.height) {
            newSize = parentSize.height
          }
          if (newSize > winSize.height) {
            newSize = winSize.height
          }
          break
        case 'left':
          newSize = size.width + x
          if (newSize > parentSize.width) {
            newSize = parentSize.width
          }
          if (newSize > winSize.width) {
            newSize = winSize.width
          }
          break
      }
      if (this.maxSize > 0 && this.maxSize < newSize) {
        newSize = this.maxSize
      }
      this.resizeValue = newSize < this.minSize ? this.minSize : newSize
      this.$nextTick(() => {
        this.$emit('resize', {size: this.resizeValue})
      })
    },
    mouseUpHandler() {
      this.mousedown = false
    },
    onKeydown(e) {
      if (!this.isVisible) {
        return
      }
      if (e.keyCode !== 27 && e.which !== 27) {
        return
      }
      // å¿½ç¥è¾å¥ç»ä»¶
      if (['INPUT', 'TEXTAREA'].indexOf(e.target.tagName) !== -1 || e.target.contentEditable === 'true') {
        return
      }
      this.toggle(false)
      return false
    },
    emitOpenEvent() {
      if (this.disableAnimation) {
        // ç¦ç¨å¨ç»æ¶ä¸éè¦ç­å¾
        this.$emit('open', this.$refs.layout)
        return
      }
      // å¼å¯å¨ç»æ¶ï¼æä¸ª318msçå¨ç»
      setTimeout(() => {
        this.$emit('open', this.$refs.layout)
      }, 318)
    },
    emitCloseEvent() {
      if (this.disableAnimation) {
        // ç¦ç¨å¨ç»æ¶ä¸éè¦ç­å¾
        this.$emit('closed')
        return
      }
      // å¼å¯å¨ç»æ¶ï¼æä¸ª318msçå¨ç»
      setTimeout(() => {
        this.$emit('closed')
      }, 318)
    }
  },
  mounted() {
    this.appendComponentTo()
    if (this.allowResize) {
      // ç»å®é¼ æ äºä»¶
      document.addEventListener('mousemove', this.mouseMoveHandler)
      document.addEventListener('mouseup', this.mouseUpHandler)
    }
    if (!this.ignoreEsc) {
      this.$el.addEventListener('keydown', this.onKeydown)
    }
    this.extensionButtons = this.$refs.buttons
  },
  beforeDestroy() {
    if (!this.ignoreEsc) {
      this.$el.removeEventListener('keydown', this.onKeydown)
    }
    if (this.allowResize) {
      // ç§»é¤äºä»¶
      document.removeEventListener('mousemove', this.mouseUpHandler)
      document.removeEventListener('mouseup', this.mouseMoveHandler)
    }
    if (!this.isVisible) {
      return
    }
    this.setVisibleValue(false)
  }
}

export default component
